    counter: function (obj, time) {
      var initTime = time;

      function timer() {
        if (initTime < 0) {
          obj.prop("disabled", false);
          obj.html("");
          initTime = time;
        } else {
          obj.prop("disabled", true);
          obj.html("(" + initTime + ")");
          initTime--;
          setTimeout(timer, 1000);
        }
      }

      timer();
    },
