/* eslint-disable */
let vm
export default {
  name: 'geo',
  data() {
    return {
      geoData: null
    }
  },
  props: {
    cache: {
      type: Boolean,
      default: true
    },
    visible: {
      type: Boolean,
      default: false
    },
    timeout: {
      type: Number,
      default: 8000
    },
    events: {
      type: Object
    }
  },
  beforeMount() {
  },
  mounted() {
    vm = this
    vm.initGeo()
    /*vm.$nextTick(function () {})*/
  },
  /*computed: {},*/
  /*watch: {},*/
  methods: {
    /**/
    initGeo() {
      /***** ChromeIOS10HTTPS******/
      // 
      if (vm.cache) {
        if (me.sessions.get('cur5656Geo')) {
          vm.$emit('on-geo-end', JSON.parse(me.sessions.get('cur5656Geo')))
          return
        }
      } else {
        me.sessions.remove('cur5656Geo')
      }

      // 01.
      var map, geolocation, citysearch
      map = new AMap.Map('geo-map', {
        resizeEnable: true
      })

      // 02.
      setTimeout(function () {
        var tips = document.getElementById("tip")
        try {
          /*  */
          function geoByBrowser() {
            // 
            var onComplete = function (data) {
              if (!data.formattedAddress) {
                geoByIp()
                return
              }
              console.log(data, '')
              var tmp = {
                source: 'browser',
                address: data.formattedAddress,
                province: data.addressComponent.province,
                city: data.addressComponent.city,
                provinceCode: data.addressComponent.citycode,
                cityCode: data.addressComponent.adcode,
                lng: data.position.lng,
                lat: data.position.lat
              }
              vm.cache ? me.sessions.set('cur5656Geo', JSON.stringify(tmp)) : null
              vm.$emit('on-geo-end', tmp)
              vm.geoData = data
              var str = ['']
              str.push('' + data.position.getLng())
              str.push('' + data.position.getLat())
              if (data.accuracy) {
                str.push('' + data.accuracy + ' ')
              }
              // IP
              str.push('' + (data.isConverted ? '' : ''))
              tips.innerHTML = str.join('<br>')
            }

            // 
            var onError = function (data) {
              geoByIp()
              // tips.innerHTML = ''
              // vm.$emit('on-geo-end', data)
            }

            map.plugin('AMap.Geolocation', function () {
              geolocation = new AMap.Geolocation({
                enableHighAccuracy: true, // :true
                timeout: vm.timeout, // 8s/
                buttonOffset: new AMap.Pixel(10, 20),// Pixel(10, 20)
                zoomToAccuracy: true, // false
                buttonPosition: 'RB'
              })
              map.addControl(geolocation)
              geolocation.getCurrentPosition()
              AMap.event.addListener(geolocation, 'complete', onComplete)//
              AMap.event.addListener(geolocation, 'error', onError)      //
            })
          }

          /* ip */
          function geoByIp() {
            // 
            var onComplete = function (data) {
              // console.log(data, 'ip')
              // 
              var tmpLnglat=[]
              for (var i in data.bounds) {
                if(data.bounds.hasOwnProperty(i)){
                  tmpLnglat=[data.bounds[i].lng,data.bounds[i].lat]
                }
              }
              var tmp = {
                source: 'ip',
                address: data.province+data.city,
                province: data.province,
                city: data.city,
                provinceCode: null,
                cityCode: data.adcode,
                lng: tmpLnglat[0],
                lat: tmpLnglat[1]
              }
              vm.cache ? me.sessions.set('cur5656Geo', JSON.stringify(tmp)) : null
              vm.$emit('on-geo-end', tmp)
              vm.geoData = data
              var str = ['']
              str.push('' + tmpLnglat[0])
              str.push('' + tmpLnglat[1])
              tips.innerHTML = str.join('<br>')
            }

            // 
            var onError = function (data) {
              tips.innerHTML = ''
              vm.$emit('on-geo-end', data)
            }

            map.plugin('AMap.CitySearch', function () {
              citysearch = new AMap.CitySearch({
                enableHighAccuracy: true, // :true
                timeout: vm.timeout, // 8s/
                buttonOffset: new AMap.Pixel(10, 20), // Pixel(10, 20)
                zoomToAccuracy: true, // false
                buttonPosition: 'RB'
              })
              map.addControl(citysearch)
              // IP
              citysearch.getLocalCity(function (status, result) {
                if (status === 'complete' && result.info === 'OK') {
                  if (result && result.city && result.bounds) {
                    var cityinfo = result.city
                    var citybounds = result.bounds
                    // 
                    map.setBounds(citybounds)
                  }
                }
              })
              AMap.event.addListener(citysearch, 'complete', onComplete) // 
              AMap.event.addListener(citysearch, 'error', onError) //
            })
          }

          geoByBrowser()
        } catch (e) {
          console.log(e)
        }
      }, 0)
    }
  }
}



