export default {
  // state
  state: {
    name: '',
    show: false,
    count: 0
  },
  // vuecomputed
  getters: {
    notShow (state) {
      // statestate
      return !state.show
    }
  },
  // 
  mutations: {
    updateCart (state, data) {
      // statestate
      state.count = data
    }
  },
  // 
  actions: {
    switchDialog (context) {
      // context$store
      context.commit('switchDialog')
      // mutations
    }
  }
}



